import { FormGroup } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { SupFormsService } from '../sup-forms.service';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore } from '@angular/fire/firestore';
import { noop } from 'rxjs';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styles: [
  ]
})
export class SignupComponent implements OnInit {

  loading: boolean;
  error: boolean;
  form: FormGroup;
  success: boolean;
  uid: string = "";

  constructor(
    private supForms: SupFormsService,
    private afa: AngularFireAuth,
    private afs: AngularFirestore,
  ) {

    this.error = false;
    this.loading = false;
    this.success = false;
    this.form = this.supForms.marketingPartnerRegistration();
  }

  ngOnInit(): void {

  }

  save() {
    this.uid = "";
    this.loading = true;
    this.error = false;
    this.success = false;
    let formUser = this.form.value;
    this.afa.createUserWithEmailAndPassword(formUser.emailAddress, formUser.password).then(createdResponse => {
      let user = createdResponse.user;
      this.uid = `Please Copy and Save this Referral Code "${user?.uid}" `
      user?.updateProfile({
        displayName: formUser.name
      });

      let userMeta = {
        name: formUser.name,
        email: formUser.emailAddress,
        role: 'marketing-partner'
      };

      this.afs.collection("Users").add(userMeta).then(_ => {
        this.form.reset();
        this.success = true;
      });
      this.loading = false;
    }, _ => {
      this.loading = false;
      this.error = true;
      noop();
    })
  }


}
