import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore } from '@angular/fire/firestore';

@Component({
  selector: 'app-marketing-partner',
  templateUrl: './marketing-partner.component.html',
  styles: [
  ]
})
export class MarketingPartnerComponent implements OnInit {

  currentUser: any;
  invitedUsers: any[];
  loading: boolean;

  constructor(
    public afa: AngularFireAuth,
    public afs: AngularFirestore
  ) {

    this.invitedUsers = [];
    this.loading = false;
  }

  ngOnInit(): void {

    this.loadData();
  }

  private loadData(): void {
    this.loading = true;
    this.afa.authState.subscribe(user => {
      this.currentUser = user;
      this.afs.collection("Applications").ref.where("referralId", '==', user?.uid).get().then(response => {
        this.invitedUsers = response.docs.map(el => el.data());
        this.loading = false;
      })
    });
  }

}
