import { Component, OnInit } from '@angular/core';
import { AppService } from '../app.service';

@Component({
  selector: 'app-registration-challan-form',
  templateUrl: './registration-challan-form.component.html',
  styleUrls: ['./registration-challan-form.component.scss']
})
export class RegistrationChallanFormComponent implements OnInit {

  data: any;
  constructor(private appService: AppService) { }

  ngOnInit(): void {
    this.loadData();
  }

  private loadData(): void {
    this.appService.registeredStudent$.subscribe(resp => {
      this.data = resp;
    })
  }

}
