import { RouterModule, Routes } from '@angular/router';

import { AboutUsComponent } from './about-us/about-us.component';
import { AdmissionProcessComponent } from './admission-process/admission-process.component';
import { AmbassadorSignupComponent } from './ambassador-signup/ambassador-signup.component';
import { ApplyNowComponent } from './apply-now/apply-now.component';
import { ChallanFormComponent } from './challan-form/challan-form.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { FaqsComponent } from './faqs/faqs.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { HomeComponent } from './home/home.component';
import { MarketingPartnerComponent } from './marketing-partner/marketing-partner.component';
import { NgModule } from '@angular/core';
import { RedirectLoggedInService } from './redirect-logged-in.service';
import { RegistrationChallanFormComponent } from './registration-challan-form/registration-challan-form.component';
import { SignInComponent } from './sign-in/sign-in.component';
import { SignupComponent } from './signup/signup.component';
import { StudentRegistrationComponent } from './student-registration/student-registration.component';
import { TermsAndConditionsComponent } from './terms-and-conditions/terms-and-conditions.component';
import { UpcomingCoursesComponent } from './upcoming-courses/upcoming-courses.component';
import { UserComponent } from './user/user.component';

const routes: Routes = [
  {
    path: "user",
    component: UserComponent,
    children: [
      {
        path: "home",
        component: HomeComponent
      },
      {
        path: 'marketing-partner-dashboard',
        component: MarketingPartnerComponent
      },
      {
        path: "sign-in",
        component: SignInComponent,
        canActivate: [RedirectLoggedInService]
      },
      {
        path: 'apply-now',
        component: ApplyNowComponent
      },
      {
        path: 'forgot-password',
        component: ForgotPasswordComponent
      },
      {
        path: 'marketing-partner',
        component: SignupComponent
      },
      {
        path: 'upcoming-courses',
        component: UpcomingCoursesComponent
      },
      {
        path: 'admission-process',
        component: AdmissionProcessComponent
      },
      {
        path: 'faqs',
        component: FaqsComponent
      },
      {
        path: 'about-us',
        component: AboutUsComponent
      },
      {
        path: 'ambassador-signup',
        component: AmbassadorSignupComponent
      },
      {
        path: 'contact-us',
        component: ContactUsComponent
      },
      {
        path: "terms-and-conditions",
        component: TermsAndConditionsComponent
      },
      {
        path: "registration-challan-form",
        component: RegistrationChallanFormComponent
      },
      {
        path: "challan-form/:id",
        component: ChallanFormComponent
      },
      {
        path: "student-registration",
        component: StudentRegistrationComponent
      },
      {
        path: "",
        pathMatch: 'full',
        redirectTo: 'home'
      }
    ]
  },
  {
    path: "Admin",
    loadChildren: () => import('./admin/admin.module').then(m => m.AdminModule)
  },
  {
    path: '',
    pathMatch: 'full',
    redirectTo: 'user'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
