import { Component, OnInit } from '@angular/core';

import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore } from '@angular/fire/firestore';
import { FormGroup } from '@angular/forms';
import { GoogleAuthService } from '../google-auth.service';
import { Router } from '@angular/router';
import { SupFormsService } from '../sup-forms.service';

@Component({
  selector: 'app-sign-in',
  templateUrl: './sign-in.component.html',
  styles: [
  ]
})
export class SignInComponent implements OnInit {

  loading: boolean;
  form: FormGroup;
  error: boolean;

  constructor(
    private router: Router,
    private afa: AngularFireAuth,
    private afs: AngularFirestore,
    private auth: GoogleAuthService,
    private supForms: SupFormsService,
  ) {
    this.error = false;
    this.loading = false;
    this.form = this.supForms.signIn();
  }

  ngOnInit(): void {
  }

  handleSubmit(): void {
    this.loading = true;
    this.error = false;
    let formResponse = this.form.value;
    this.afa.signInWithEmailAndPassword(formResponse.emailAddress, formResponse.password).then(_ => {
      this.router.navigateByUrl("/user/home")
      this.loading = false;
    }, _ => {
      this.error = true;
      this.loading = false;
    })
  }

  async forgotPassword(): Promise<void> {
    this.router.navigateByUrl("/user/forgot-password");
  }
}
