import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { HotToastService } from '@ngneat/hot-toast';
import { ContactUsAngularService } from '../Services/contact-us-angular.service';

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.scss']
})
export class ContactUsComponent implements OnInit {

  loading: boolean = false;
  form = new FormGroup({
    name: new FormControl(null, [Validators.required]),
    emailAddress: new FormControl(null, [Validators.required, Validators.email]),
    subject: new FormControl(null, [Validators.required]),
    message: new FormControl(null, [Validators.required])
  })

  constructor(
    private contactService: ContactUsAngularService,
    private toast: HotToastService,
  ) { }

  ngOnInit(): void {
  }

  create(): void {
    this.loading = true;
    let processing = this.toast.loading("Processing");
    let request = this.form.value;
    this.contactService.create(request).then(_ => {
      this.form.reset();
      this.toast.success("Operation Successful");
      processing.close();
      this.loading = false;
    }, _ => {
      this.toast.error("Operation Failed");
      processing.close();
      this.loading = false;
    })
  }
}
