import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AppService {

  private registeredStudentSubject: BehaviorSubject<any>;
  registeredStudent$: Observable<any>;

  constructor() {
    this.registeredStudentSubject = new BehaviorSubject(null);
    this.registeredStudent$ = this.registeredStudentSubject.asObservable();
  }

  updateRegisteredStudent(data: any): void {
    this.registeredStudentSubject.next(data);
  }

}
