import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { AngularFirestore } from '@angular/fire/firestore';
import { AngularFireAuth } from '@angular/fire/auth';

@Injectable({
  providedIn: 'root'
})
export class RedirectLoggedInService implements CanActivate {

  constructor(
    private router: Router,
    private afs: AngularFirestore,
    private afa: AngularFireAuth
  ) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | UrlTree | Observable<boolean | UrlTree> | Promise<boolean | UrlTree> {

    let response: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(true);
    this.afa.authState.subscribe(currentUser => {
      console.log(currentUser);
      if (currentUser == null) {
        response.next(true);
      } else {
        let user = this.afs.collection('Users').ref.where("email", "==", currentUser.email).get().then(el => el.docs[0].data() as any);
        user.then(resp => {
          if (resp?.role == 'admin') {
            this.router.navigateByUrl("/Admin")
            response.next(false);
          } else if (resp?.role == 'marketing-partner') {
            this.router.navigateByUrl("/user/marketing-partner-dashboard");
            response.next(false);
          } else {
            this.router.navigateByUrl("/user/");
            response.next(false);
          }
        });
      }
    })

    return response.asObservable();
  }
}
