import { Injectable } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore } from '@angular/fire/firestore';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { BehaviorSubject, noop, Observable } from 'rxjs';
import {
  map, mapTo
} from "rxjs/operators";

@Injectable({
  providedIn: 'root'
})
export class AdminOnlyService implements CanActivate {

  constructor(
    private afa: AngularFireAuth,
    private afs: AngularFirestore,
    private router: Router
  ) { }
  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | UrlTree | Observable<boolean | UrlTree> | Promise<boolean | UrlTree> {
    return this.afa.currentUser.then(currentUser => {
      if (currentUser != null) {
        return this.afs.collection("Users").ref.where("email", '==', currentUser.email).get().then(el => {
          let userDoc = el.docs[0].data() as any;
          if (userDoc.role == 'admin') {
            return true;
          } else {
            this.afa.signOut().then(_ => noop());
            this.router.navigateByUrl("/user/sign-in");
            return false;
          }
        })
      } else {
        this.router.navigateByUrl("/user/sign-in");
        return false;
      }
    }).then(response => response)
  }
}
